<template>
	
<div class="saleDetail_page">

	
<header-top title='' back></header-top>
<div class="fixed-top-div"></div>

<div class="content" id='wrapper_box'>
	

  <div class="order-info" style='padding-bottom:2.1rem;' id="wrapper">
  <!-- banner -->
  <template id='template' class="swiper-container banner">

	  <swiper :options="swiperOption"  class="swiper-wrapper banner-wrapper">
	    <swiper-slide class="swiper-item" :key='index' v-for=" (item,index) in mainList ">
	    	<img :src='item'>
	    	<span v-if='priList[index]' >{{ priList[index] }}</span>
	    </swiper-slide>
	    <div class="swiper-pagination" slot="pagination"></div>
	  </swiper>

    <p class="link-href">
      <span class="share-icon right"><img src="../../../assets/imgs/xhjy/xqy2.png"></span>

      <span v-if="favoriteState" class="favorite right" @click="removeFavorite"><img src="../../../assets/imgs/xhjy/xqy4.png"/> </span>
      <span v-else class="favorite right" @click="addFavorite"><img src="../../../assets/imgs/xhjy/xqy3.png"/> </span>


    </p>
  </template>

  <!--banner-->

    <div class="status">
        <p v-if="TenderInfo.t_price">
            <span class="lt"></span>
            <span class="price">{{TenderInfo.t_price}}</span>
            <span class="unit">{{TenderInfo.bz_name}}/{{TenderInfo.dw_name}}</span>
        </p>
        <p v-else>
            <span></span>
        </p>

        <p>
            <span class="block"></span>
            <span v-if='tenderStatus'>
              <i class="i-day" >{{timer[0]}}</i>
              <i class="i-hour" >{{timer[1]}}</i>
              <i class="i-min" >{{timer[2]}}</i>
              <i class="i-sec" >{{timer[3]}}</i>
            </span>
            <span v-else style="color:red;"></span>
        </p>

    </div>
    <div class="desc">
        <h3>
            <img v-if="TenderInfo.vipState==2" src="../../../assets/imgs/icon_vip2.png"/>
            <img v-if="TenderInfo.vipState==1" src="../../../assets/imgs/icon_vip22.png"/>
            <span v-text="TenderInfo.t_title" id="title"></span>
        </h3>

        <section>
            <p><span></span>{{TenderInfo.t_id}}</p>
            <p><span></span>{{TenderInfo.a_bail}}</p>
            <p><span></span>{{TenderInfo.t_quality}}{{TenderInfo.dw_name}}</p>
            <p><span></span>{{TenderInfo.pack_name}}</p>
            <p><span></span>{{TenderInfo.t_type}}</p>
            <p><span></span>{{TenderInfo.dz_name}}</p>
            <p><span></span>{{TenderInfo.bz_name}}</p>
            <p><span></span>{{TenderInfo.jy_name}}</p>
        </section>

        <div class="tips">
            <span class="tips-tit font-style-red"></span>
            <span><i class='font-style-red'>7</i></span>

        </div>
        <div class="info">
            <p>{{TenderInfo.t_detail}}</p>
        </div>

    </div>
    <div class="detail-img">
        <li v-for="(item,index) in imgsList ">
            <img v-bind:src="item" alt=""/>
            <p>{{ detList[index] }}</p>
        </li>
    </div>
    <div style='height:2.1rem;'></div>
  </div>



<!--   <div  style="position: absolute;width:100%;bottom:0;">
      <div class="btn">

          <router-link :to="{path:'/sale/submitMargin',query:{id:TenderInfo.t_id}}"></router-link>
          <a @click='showDeal'></a>
          <router-link :to="{path:'/sale/toBid',query:{id:TenderInfo.t_id}}"></router-link>

      </div>
  </div>
 -->



  <div v-if="TenderInfo.finish_state != 1 || tenderStatus != false" style="position: fixed;width:100%;bottom:0;">
      <div class="btn">

        <a @click="submitBond"></a>

        <a @click=" showDeal " v-show=" TenderInfo.t_price !='' ||TenderInfo.t_price !=0 "></a>

        <a @click=" goOffer ">{{ TenderInfo.t_price==''?'':'' }}</a>
        
      </div>
  </div>

  <transition>
    <loading v-if='showLoading'></loading>
  </transition>



</div>


<transition>
  <router-view></router-view>
</transition>

</div>


</template>
<script>


// console.log(layui);


// import layer from '@/plugins/layer/layer'


import {checkLogin,checkLogin2,checkAndToAuthen,checkAuthen,addFootPrint,addFavorite} from '@/tool/tool'



import loading from '@/components/common/loading'


import {formatTime} from '@/config/util'

import headerTop from '@/components/header/header'

import bScroll from 'better-scroll'
	
	export default{
		data(){
			return{

		    //
	      	swiperOption: {
	          pagination: '.swiper-pagination',
	          direction: 'horizontal',
	          slidesPerView: 1,
	          paginationClickable: true,
	          spaceBetween: 1,
	          loop:true,
            autoPlay:true,
	          mousewheelControl: true
	        },
          TenderInfo:{},
          timer:[],
          tenderStatus:true,//
          bondState:null,//
          scroller:null,
          showLoading:false,
          favoriteState:false,//
			}
		},
		components:{
			headerTop,
      loading,
      // SweetModal,
      // SweetModalTab
		},
    mounted(){

      this.$nextTick(()=>{
        // return;
        //
        layer.open({type:2,shadeClose:false,shade:'background-color: rgba(0,0,0,.6)',})

        setTimeout(()=>{
          //
          layer.closeAll();
          this.scroller = new bScroll('#wrapper_box',{
            momentum: true,//
            bounce:false,//
            deceleration: 0.003,//0.001 0.01
            HWCompositing: false,//
            swipeTime: 2000,//2500 swipe 
            click: true, //
          })
        },1000)


        //
        if(!checkLogin()) return;
        //
        addFootPrint(this,this.$route.query.id,"TB");

        //
        this.queryFavorite();

        //
        this.queryBond();

      })

      // this.byScroll();
    },
    computed:{


      // 
      mainList(){
        if(this.TenderInfo.p_file){
          let srcs =  this.TenderInfo.p_file.split(',');
          return srcs.slice(0,srcs.length-1);
        }

        return []
      },
      //
      priList(){
        return this.TenderInfo.priList;
      },
      //
      detList(){
        return this.TenderInfo.detList;
      },
      //
      imgsList(){

        if(this.TenderInfo.p_detail){
          let srcs =  this.TenderInfo.p_detail.split(',');
          return srcs.slice(0,srcs.length-1);
        }else{
          return [];
        }
      }
    },
    created(){
      this.queryTenderById();

    },
    methods:{


      //
      submitBond(){

        if(!checkLogin2(this)){
          return;
        }

        if(!checkAndToAuthen(this)){
          return;
        }

        if(this.bondState.code != 0){
          layer.msg(this.bondState.message);
            return;
        }else if(this.bondState.code == 0){
          layer.msg("");
          return;
        }

        //
        this.$router.push({path:'/sale/saleDetail/submitMargin',query:{id:this.TenderInfo.t_id}});


      },

      //
      queryFavorite(){
        let currentTenderId = this.$route.query.id;//ID
        this.$http.post("/queryShoucang",{vip:this.$store.getters.getVip}).then((res)=>{
          let list = res.data.response['TenderVo'];
          if(list){
            for(var i = 0;i < list.length;i++){
                if( list[i] && list[i].t_id==currentTenderId){
                  //
                  this.favoriteState = true;
                }
            }
          }
        })
      },
      removeFavorite(){
        let currentTenderId = this.$route.query.id;//ID
        this.$http.post("/DeleteShoucang",{vip:this.$store.getters.getVip,sid:currentTenderId}).then((res)=>{
          //
          if(res.data.code == 0 ){
            this.favoriteState = false;
          }else{
            layer.open({
              skin:'msg',
              content:res.data.response||res.data.message,
            })
          }


        })
      },
      //
      addFavorite(){
        //
        if(!checkLogin2(this)){
          // layer.msg("")
        }else{
          addFavorite(this,this.$route.query.id,"TB")
        }



      },


      //
      queryBond(){

        //queryAccess
        this.$http.post('/queryAccess',{vip:this.$store.getters.getVip,bid:this.$route.query.id}).then((res)=>{

          if(res.data.code == 0){
            this.bondState = res.data;
          }else{
            //layer.msg(res.data.message||res.data.response);
          }

        })

      },



      //
      showDeal(){


        if(!checkLogin2(this)) return;

        let vm = this;
        let tender = this.TenderInfo;
        //
        let info = tender.t_price+tender.bz_name+"/"+tender.dw_name;
        //
        let name = tender.t_title;

        layer.open({
          content:`
            <div class='modal'>
              <div class="alert-linkbox">
                <h3></h3>
                <p>${info}${name}</p>
              </div>
            </div>
          `,
          btn:['',''],
          yes:function(index){

            layer.close(index);
            //
            vm.deal();
          },
          no:function(){
            console.log("no")
          }
        })
      },

      //
      deal(){
        this.$http.post('/Deal',{vip:this.$store.getters.getVip,bid:this.TenderInfo.t_id,zbrid:this.TenderInfo.usr_id,cjid:this.$store.getters.getVip}).then((res)=>{
            console.log(res.data);
            if(res.data.code==0){
                layer.msg("");
                //
                this.tenderStatus = false;
            }else{
                layer.msg(res.data.message);
            }
        })

      },
      //
      goOffer(){
        //
        if(!checkLogin2(this)) return;
        //
        if(this.bondState!=null && this.bondState.code!=0){
          this.$router.push({path:'/sale/saleDetail/toBid',query:{id:this.TenderInfo.t_id}});
        }else{
          layer.msg("");
          return;

        }

        //


      },
      showBox(){
        
      },
      hideBox(){
        // this.$modal.hide('hello-world')
      },

      byScroll(){

        //
        setTimeout(()=>{
          //console.log("kaishi====")
          this.scroller = new bScroll(this.$refs.wrapper_box,{
            click:true,
          })
        },3000);
      },


      //
      getTime(){
        let t_date = this.TenderInfo.t_date;
        let pass_time = this.TenderInfo.pass_time;
        let timers = setInterval(()=>{
          //this.timer = formatTime(t_date,pass_time);
          if(formatTime(t_date,pass_time)){
              this.timer = formatTime(t_date,pass_time);
            }else{
              this.tenderStatus = false;
              clearInterval(timers);
            }
          },1000)

      },

      queryTenderById(){
        this.$http.post('/queryTenderByid',this.$route.query).then((res)=>{

          if(res.data.code == 0){
            this.TenderInfo = res.data.response;

            this.TenderInfo.priList = JSON.parse(this.TenderInfo.priList);
            this.TenderInfo.detList = JSON.parse(this.TenderInfo.detList);

            this.getTime();
          }

        })
      }
    },

	}
</script>
<style scoped lang='less'>

@import '../../../assets/css/variable.less';

.modal{
  position: fixed;
  top:100px;
  width:80%;
  left:0;
  right:0;
  margin:auto;
  overflow: hidden;
}

.alert-linkbox{
    background: #fff;
    text-align: center;
    padding-top: 0.625rem;
    overflow: hidden;
    border-radius: 8px;
    width: 17.5rem;
    box-sizing: border-box;
}

.saleDetail_page{

  .fixed();


}



/*//*/
.swiper-wrapper{
	position: relative;
}


.swiper-pagination{
  bottom:0px !important;
}

.banner-wrapper {
  height:180/@root-font-size;

}



.swiper-pagination-bullet-active{
  background: #66D231!important;
}

.banner-wrapper div img{
  width:100%;
  height:100%;
}
.banner-wrapper div span{
  position: absolute;
  width:100%;
  height:2rem;
  overflow: hidden;
  left:0;
  bottom:0;
  background: #dbefec;
  color: #000b35;
  font-size:.5rem;
  word-break: break-all;
  padding:3px;
  line-height:1.1em;
  text-align: left;
}

.content{
  height:100%;
}
/*
//-*/
.saleDetail_page{

  .order-info{

  	position: relative;

  }

  .img{
    position: relative;
    text-align: center;
    height: 240/@root-font-size;
    overflow: hidden;
    img{
      width:100%;
    }
  }
  .link-href{
    position: absolute;
    width:100%;
    top:0;
    z-index:99;
    span{
      margin:12/@root-font-size 8/@root-font-size;

    }
    img{
      height:25/@root-font-size;
      width:auto;
    }
  }

  .block{
    display: block;
  }

  .status{

    display: flex;
    font-size:.5rem;
    overflow: hidden;

    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;

    white-space: nowrap;
    
    
    
    p{
      flex:1;
      text-align: center;

      i{
        background: #387E02;
        padding:1/@root-font-size;
        width:2em;
        display: inline-block;
        text-align: center;
        color:#fff;
        margin:0 1px;
        border-radius:8px;
      }



    }
    p:first-child{

      background: linear-gradient(to right, #94C73F 0%,#64A01F 60%,#367D01 100%);
      overflow: hidden;
      -ms-text-overflow: ellipsis;
      text-overflow: ellipsis;
      white-space: nowrap;

      font-size:.6rem;

      display: flex;
		align-items: center;
		justify-content: center;

		span{
			color:#fff;

		}


      .price{
        font-size:1px*@font-big-size;
      }
    }
    p:last-child{
      padding:2/@root-font-size 0;
      line-height:15/@root-font-size;
      background: #EBFFDC;
      color:#377d02;
    }

  }




  .desc{
    padding:5/@root-font-size;
    /*font-size:1px*@font-middle-size;*/
    font-size:.6rem;

    h3{
      color:#000;

      line-height:40/@root-font-size;

      img{
        vertical-align: middle;
        margin:0 5px;
        height:25/@root-font-size;
      }
    }

    section{
      border-top:1px solid #DCDCDC;
      border-bottom:1px solid #DCDCDC;
      line-height:25/@root-font-size;
      padding:6/@root-font-size /*35/@root-font-size*/0;
      color:#000;
      /*font-size:.6rem;*/

      p{
        display: flex;
      }

      span{
        justify-content: flex-start;
        width:30%;
      }

    }

    .font-red{
      color:#f00;
      margin:0 3px;
    }
    .tips{
      padding:8/@root-font-size 0;
      border-bottom:1px solid #DCDCDC;
      font-size:.5rem;
      display: flex;
    }

    .tips-tit{
      display: flex;
      align-items: center;
      justify-content: center;
      width:30%;
      white-space: nowrap;

    }

  }

  .detail-img{
    li{
      margin-bottom:5px;
      padding:0 8px;
    }
    img{
      width:100% !important;
    }
    p{
      font-size:1px*@font-middle-size;
      padding:3px;
    }
  }

  .btn{
    display: flex;
    line-height:1.9rem;
    a{
      flex:1;
      text-align: center;
      color:#fff;
      font-size:.7rem;
    }
    a:first-child{
      background: #68CC1E;
    }
    a:nth-child(2){
      background: #FE9402;
    }
    a:last-child{
      background: #FF5000;
    }
  }


}





</style>


